/**
 * marcneander.se stylesheet
 *
 * This stylesheet follows the principles of writing
 * consistent, idiomatic CSS by Nicolas Gallagher.
 *
 * https://github.com/necolas/idiomatic-css/
 *
 */
/* Include normalize.css by Nicolas Gallagher */
@import "~normalize-css/normalize.css";
/* Include everything else */
/* ==========================================================================
   Typeography
   ========================================================================== */
a {
  color: #20A2FF;
  text-decoration: none;
}
a:hover,
a:focus {
  text-decoration: underline;
  outline: none;
}
h1,
h2,
h3 {
  margin: 0 0 16px;
  font-size: 24px;
}
@media (min-width: 768px) {
  h1,
  h2,
  h3 {
    margin-bottom: 22px;
  }
}
h4,
h5,
h6 {
  margin: 0 0 12px;
  font-size: 20px;
}
p {
  margin: 0 0 26px;
}
p:last-child {
  margin: 0;
}
/* ==========================================================================
   Header
   ========================================================================== */
header {
  display: flex;
  flex-flow: column nowrap;
  padding-bottom: 20px;
  color: #FFFFFF;
  background-color: #293440;
  /* Title */
  /* Arrow */
  /* Image */
}
@media (min-width: 768px) {
  header {
    padding-bottom: 30px;
  }
}
@media (min-height: 400px) {
  header {
    height: 100vh;
    padding: 0;
  }
}
header .header-title {
  flex: 0 0 112px;
  width: 100%;
  margin: 40px 0 20px;
}
@media (min-width: 768px) {
  header .header-title {
    margin: 60px 0 30px;
  }
}
header .header-title-primary,
header .header-title-secondary {
  font-family: "Roboto Condensed", Arial, Helvetica, sans-serif;
}
header .header-title-primary {
  margin-bottom: 4px;
  font-size: 44px;
  line-height: 52px;
}
@media (min-width: 768px) {
  header .header-title-primary {
    font-size: 62px;
    line-height: 75px;
  }
}
header .header-title-secondary {
  font-size: 20px;
  font-weight: normal;
  line-height: 24px;
  color: #8F9BA8;
  text-transform: uppercase;
  letter-spacing: 1px;
}
header .header-arrow-wrap {
  display: flex;
  justify-content: center;
  align-items: center;
  flex: 0 0 100px;
  width: 100%;
}
@media (max-height: 400px) {
  header .header-arrow-wrap {
    display: none;
  }
}
header .header-arrow-wrap .header-arrow {
  display: block;
  width: 30px;
  height: 30px;
  background-image: url("data:image/svg+xml;base64,");
  background-size: contain;
  animation: bounce 1s infinite;
}
header .header-arrow-wrap .header-arrow-btn {
  padding: 10px;
}
header .header-image-wrap {
  display: flex;
  flex: 1 1 auto;
  width: 100%;
  margin-bottom: 20px;
}
header .header-image-wrap .header-image {
  flex: 1 1 auto;
  max-height: 349px;
}
@media (min-width: 768px) {
  header .header-image-wrap {
    margin-bottom: 30px;
  }
}
/* ==========================================================================
   Work
   ========================================================================== */
.work {
  background: #FBFBFB;
  border-color: #F1F1F1;
  border-style: solid;
  border-width: 1px 0 1px 0;
}
.work .work-img {
  width: 100%;
  max-width: 442px;
  margin-bottom: 16px;
}
/* ==========================================================================
   Contact
   ========================================================================== */
.contact-avatar-img {
  margin-bottom: 16px;
}
.contact-heading {
  margin-bottom: 4px;
}
/* ==========================================================================
   Footer
   ========================================================================== */
footer {
  font-size: 15px;
}
footer .container {
  padding-top: 20px;
  padding-bottom: 20px;
}
footer .footer-wrap {
  display: flex;
  justify-content: space-between;
}
footer .footer-links {
  display: flex;
}
footer .footer-links a {
  display: inline-block;
  margin-left: 8px;
  text-decoration: none;
}
footer .footer-links a:hover,
footer .footer-links a:focus {
  opacity: .7;
}
footer .footer-twitter-icon,
footer .footer-github-icon {
  display: block;
  width: 32px;
  height: 32px;
  background-repeat: no-repeat;
  background-position: center center;
  background-size: 22px;
}
footer .footer-twitter-icon {
  background-image: url("data:image/svg+xml;base64,");
}
footer .footer-github-icon {
  background-image: url("data:image/svg+xml;base64,");
}
/* ==========================================================================
   Labels
   ========================================================================== */
.label {
  display: inline-block;
  padding: 0 14px;
  margin-bottom: 10px;
  font-family: "Roboto Condensed", Arial, Helvetica, sans-serif;
  font-weight: bold;
  color: #26B255;
  text-transform: uppercase;
  border: 1px solid #26B255;
}
.label:hover {
  color: #FFFFFF;
  background: #26B255;
}
.label.label-xs {
  font-size: 12px;
  line-height: 34px;
  border-radius: 17px;
}
.label.label-sm {
  font-size: 14px;
  line-height: 38px;
  border-radius: 19px;
}
.label.label-md {
  font-size: 16px;
  line-height: 42px;
  border-radius: 21px;
}
.label.label-lg {
  font-size: 18px;
  line-height: 46px;
  border-radius: 23px;
}
/* ==========================================================================
   Utilities
   ========================================================================== */
/* Bounce animation */
@keyframes bounce {
  0%,
  100% {
    transform: translateY(0);
  }
  50% {
    transform: translateY(-15px);
  }
}
html {
  height: 100%;
}
body {
  display: flex;
  flex-direction: column;
  min-height: 100%;
  font-family: "Roboto", Arial, Helvetica, sans-serif;
  font-size: 18px;
  line-height: 32px;
  color: #333333;
  text-align: center;
}
.container {
  padding: 40px 12px;
  margin-right: auto;
  margin-left: auto;
}
@media (min-width: 768px) {
  .container {
    width: 620px;
    padding: 80px 12px;
  }
}



